            :src="
              require(`../assets/img/一级按钮/item${i}-${
                page == i ? '选中' : '未选中'
              }.png`)
            "
            alt=""
          />
        </div>
      </div>
    </header>
  </div>
</template>

<script>
export default {
  data() {
    return {
      navarr: [
        { id: 1, name: "云算力" },
        { id: 2, name: "全光网" },
        { id: 3, name: "5G精品网" },
        { id: 4, name: "首页" },
      
      ],
    };
  },
  methods: {
    //改变hash值
    navchange(i) {
      this.$router.push({ name: this.navarr[i].name });
    },
  },
  props: ["page"],
};
</script>

