      &:first-child {
        position: absolute;
        top: 25px;
        left: 190px;
      }
      &:nth-child(2) {
        position: absolute;
        top: 25px;
        left: 594px;
      }
      &:nth-child(3) {
        position: absolute;
        top: 25px;
         right: 592px;
