import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: '首页',
    component: HomeView
  },
  {
    //云算力
    path: '/cloud',
    name: '云算力',
    component: () => import(/* webpackChunkName: "about" */ '../views/CloudView.vue')
  },
  {
    //全光网
    path: '/light',
    name: '全光网',
    component: () => import('../views/AllLight.vue')
  },
  {
    //5G精品网
    path: '/5g',
    name: '5G精品网',
    component: () => import('../views/5GView.vue')
  },
  {
    //5G精品网案例
    path: '/5gcase',
    name: '5G精品网案例',
    component: () => import('../views/5GCase.vue')
  },
  {
    //云算力案例
    path: '/cloudcase',
    name: '云算力案例',
    component: () => import('../views/CloudCase.vue')
  },
  {
    //全光网案例
    path: '/lightcase',
    name: '全光网案例',
    component: () => import('../views/LightCase.vue')
  },

]

const router = new VueRouter({
  routes
})
//防止hash值重复的声明
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push (location) {
    return originalPush.call(this, location).catch(err => err)
}
export default router
